.questions-page {
    .questions-container {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .question-element {
        background: #fff;
        display: flex;
        flex-direction: row;
        align-items: stretch;
        padding: 12px 8px;
        margin: 10px 0;
        min-height: 150px;
        width: 100%;

        * {
            background: #fff;
        }

        .stats-container {
            color: #6a737c;
            display: block;
            text-align: center;
            font-size: 12px;
            margin-left: 8px;
            margin-right: 16px;
            width: auto;
            height: auto;
        }

        .stats-container * {
            display: block;
        }

        .stats-container strong {
            font-size: 30px;
            font-weight: 400;
        }

        .stats-container {
            .status {
                padding: 0 0 5px;
                border-radius: 3px;
                margin-bottom: 4px;

            }

            .views {
                padding-top: 4px;
            }
        }

        .summary {
            border-left: 1px solid #eff0f1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            overflow: hidden;
            padding-left: 15px;
            width: 100%;

            h3 {
                font-size: 18px;
                line-height: 1.4;
                margin-bottom: .5em;
                word-wrap: break-word;
                word-break: break-word;
            }
        }

        .started {
            align-items: flex-end;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
        }

        .tag {
            background: #fafafa;
            border-radius: 2px;
            padding: 5px;
            margin-right: 5px;
        }

        .user-info {
            align-items: center;
            display: flex;
            text-align: right;
            flex-direction: row;
        }

        .user-info > * {
            margin-right: 10px;
        }

        .answered {
            color: #377D21;
        }

        .user-icon {
            width: 32px;
            height: 32px;
        }
    }
}

@media (max-width: 576px) {
    .questions-page {
        .question-element {
            text-align: center;
            flex-direction: column;
            padding-bottom: 0;

            .summary {
                border: 0;
            }

            .stats-container {
                border-top: 1px solid #eff0f1;
                display: flex;
                flex-direction: column;
                margin-top: 5px;
                padding-top: 2px;
                order: 2;
            }

            .stats {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                order: 2;
            }

            .started {
                align-items: center;
                flex-direction: column;

                & > * {
                    margin-top: 10px;
                }
            }

            .publish-time::before {
                content: 'publish date: ';
            }
        }
    }
}